  font-weight: bolder;
  font-size: 15px;
}

.ant-card-extra {
  .ant-select-selection {
    max-width: 150px;
    background-color: rgba($color: #dadada, $alpha: 0.1);
                color: #dadada;
  }
}

.anticon-warning, .anticon-zoom-out {
  color: #dadada;
  margin-left: 10px;
  font-size: 20px;
  // vertical-align: middle;
}

.static-series-container {

  g.stack._0>path {
    stroke-width: 3px;
  }
  
